import React, { Component } from 'react'
import PropTypes from 'prop-types'
import classnames from 'classnames'
import ConditionDiv from './../../components/conditionDiv'
import Header from './header'
import CoinRatingSection from './coin-rating-section'
import CoinAnalysisSection from './coin-analysis-section'
import CoinDescriptionSection from './coin-description-section'
import CoinRatingsTrendSection from './coin-ratings-trend-section'
import CoinPricePredictionSection from './coin-price-prediction-section'
import CoinNewsSection from './coin-news-section'
import './coin-ratings-layout.scss'

class CoinRatingsPage extends Component {
  render() {
    const coinRatingData = (this.props.coinRatingData || {}).coinRatingData
    const coin = (this.props.coinRatingData || {}).coin
    const coinSettings = (this.props.coinRatingData || {}).coinSettings || {}

    return (
      <div
        className={classnames('coin-ratings-layout', {
          loaded: this.props.loaded,
        })}
      >
        <Header coin={coin} />

        <div className="container">
          <div className="coin-rating-body">
            <ConditionDiv className="col-6" condition={coinSettings.showRating}>
              <CoinRatingSection coinRatingData={coinRatingData} coin={coin} />
            </ConditionDiv>
            <ConditionDiv
              className="col-6"
              condition={coinSettings.showDescription}
            >
              <CoinDescriptionSection coin={coin} coinSettings={coinSettings} />
            </ConditionDiv>
            <ConditionDiv
              className="col-12"
              condition={coinSettings.showAnalysis}
            >
              <CoinAnalysisSection coin={coin} coinSettings={coinSettings} />
            </ConditionDiv>
            <ConditionDiv
              className="col-12"
              condition={coinSettings.showRatingTrend}
            >
              <CoinRatingsTrendSection
                coinRatingData={coinRatingData}
                coin={coin}
              />
            </ConditionDiv>
            <ConditionDiv
              className="col-12"
              condition={
                coinSettings.showCoinPricePrediction ||
                coinSettings.showCryptocurrencyPricePrediction
              }
            >
              <CoinPricePredictionSection
                coinRatingData={this.props.coinRatingData}
                coin={coin}
                showCoinPricePrediction={coinSettings.showCoinPricePrediction}
                showCryptocurrencyPricePrediction={
                  coinSettings.showCryptocurrencyPricePrediction
                }
              />
            </ConditionDiv>
            <ConditionDiv className="col-12" condition={coinSettings.showNews}>
              <CoinNewsSection coin={coin} />
            </ConditionDiv>
          </div>
        </div>
      </div>
    )
  }
}

CoinRatingsPage.propTypes = {
  coinRatingData: PropTypes.object,
  loaded: PropTypes.bool,
}

export default CoinRatingsPage
