import React, { Component } from 'react'
import PropTypes from 'prop-types'
import classnames from 'classnames'
import ConditionDiv from './../../../components/conditionDiv'
import DirectionPrediction from './directionPrediction'
import WorthPrediction from './worthPrediction'
import './coin-price-sentiment-section.scss'

class CoinPriceSentimentSection extends Component {
  getSectionHeaderText = () => {
    const subTitles = [
      this.props.showCryptocurrencyPricePrediction ? 'Cryptocurrency' : null,
      this.props.showCoinPricePrediction
        ? `${(this.props.coin || {}).coinName} Price`
        : null,
    ]

    return subTitles.filter((x) => x !== null).join(' & ') + ' Prediction'
  }

  render() {
    return (
      <div className="coin-price-sentiment-section section">
        <h3 className="section-header">{this.getSectionHeaderText()}</h3>
        <div className="section-body">
          <ConditionDiv
            condition={this.props.showCryptocurrencyPricePrediction}
          >
            <DirectionPrediction
              valuation={(this.props.coinRatingData || {}).valuation}
              className={classnames({
                'full-width': !this.props.showCoinPricePrediction,
              })}
            />
          </ConditionDiv>

          <ConditionDiv condition={this.props.showCoinPricePrediction}>
            <WorthPrediction
              stats={(this.props.coinRatingData || {}).stats}
              coin={this.props.coin}
              className={classnames({
                'full-width': !this.props.showCryptocurrencyPricePrediction,
              })}
            />
          </ConditionDiv>
        </div>
      </div>
    )
  }
}

CoinPriceSentimentSection.propTypes = {
  coinRatingData: PropTypes.object,
  coin: PropTypes.object,
  showCoinPricePrediction: PropTypes.bool,
  showCryptocurrencyPricePrediction: PropTypes.bool,
}

export default CoinPriceSentimentSection
