import React, { Component } from 'react'
import Indicator from './indicator'
import numeral from 'numeral'
import PropTypes from 'prop-types'

class Forecast extends Component {
  render() {
    const currentPrice =
      (((this.props.coinRatingData || {}).conversion || {}).lastPrices || {})
        .price || 0
    const priceTarget =
      ((this.props.coinRatingData || {}).rating || {}).priceTarget || 0
    const ratio = (priceTarget / currentPrice - 1) * 100
    const direction = ratio > 0 ? 'higher' : 'lower'
    const side = ratio > 0 ? 'UPSIDE' : 'DOWNSIDE'

    return (
      <div className="coin-rating-section-item forecast">
        <Indicator value={numeral(ratio).format('0')} text={side} />
        <div className="content">
          <div className="title">FORECAST</div>
          <div className="payload">
            The 12-month price target of{' '}
            {numeral(priceTarget).format('$0,0.00')} is{' '}
            {numeral(ratio).format('0')}% {direction} than the current price of{' '}
            {numeral(currentPrice).format('$0,0.00')}.
          </div>
        </div>
      </div>
    )
  }
}

Forecast.propTypes = {
  coinRatingData: PropTypes.object,
}

export default Forecast
