import React, { Component } from 'react'
import PropTypes from 'prop-types'
import numeral from 'numeral'
import classnames from 'classnames'
import PriceIndicator from './../../../components/price-indicator'

class CoinHeaderSection extends Component {
  render() {
    if (!this.props.coin) {
      return null
    }

    const coin = this.props.coin
    const coinImage = coin.imageUrl
    const price =
      (((coin.conversions || {}).USD || {}).lastPrices || {}).price || 0
    const supply = coin.supply || 0
    const marketCap = supply * price
    const changePrc7Day =
      (((coin.conversions || {}).USD || {}).lastPrices || {}).changePrc7Day || 0
    const changePrc24Hour =
      (((coin.conversions || {}).USD || {}).lastPrices || {}).changePrc24Hour ||
      0

    return (
      <div className="coin-header-section">
        <div
          className="coin-image"
          style={{ backgroundImage: `url(${coinImage})` }}
        />
        <div className="coin-data">
          <h2 className="coin-name">{`${coin.coinName} ${coin.symbol}`}</h2>
          <div className="coin-indicators">
            <div className="indicator">
              <div className="label">PRICE</div>
              <div className="value">
                <PriceIndicator
                  fromSymbol={coin.name}
                  toSymbol="USD"
                  initialPrice={price}
                />
              </div>
            </div>
            <div className="indicator">
              <div className="label">MARKET CAP</div>
              <div className="value">
                $
                {numeral(marketCap)
                  .format('0.0a')
                  .toUpperCase()}
              </div>
            </div>
            <div
              className={classnames(
                'indicator',
                changePrc24Hour > 0 ? 'green' : 'red',
              )}
            >
              <div className="label">24H</div>
              <div className="value">
                {numeral(changePrc24Hour).format('+0.00')}%
              </div>
            </div>
            <div
              className={classnames(
                'indicator',
                changePrc7Day > 0 ? 'green' : 'red',
              )}
            >
              <div className="label">7D</div>
              <div className="value">
                {numeral(changePrc7Day).format('+0.00')}%
              </div>
            </div>
          </div>
        </div>
      </div>
    )
  }
}

CoinHeaderSection.propTypes = {
  coin: PropTypes.object,
}

export default CoinHeaderSection
