import React, { Component } from 'react'
import PropTypes from 'prop-types'
import numeral from 'numeral'

const capitalizeFirstLetter = (str) => {
  if (typeof str === 'string') {
    return str.split(' ').map((s) => s.charAt(0).toUpperCase() + s.slice(1))
  }

  return ''
}

class CoinRatingsTrendTable extends Component {
  render() {
    return (
      <table
        className="coin-ratings-trend-table"
        cellSpacing="0"
        cellPadding="0"
      >
        <thead>
          <tr>
            <th className="first-col">HISTORICAL RATINGS</th>
            <th>OVERALL RATING</th>
            <th>BUY %</th>
            <th>HODL %</th>
            <th>SELL %</th>
            <th className="last-col">LSI</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <th className="first-col">Current</th>
            <td>{capitalizeFirstLetter(this.props.rating.rating || 'none')}</td>
            <td>{numeral(this.props.rating.buy).format('0')}%</td>
            <td>{numeral(this.props.rating.hodl).format('0')}%</td>
            <td>{numeral(this.props.rating.sell).format('0')}%</td>
            <td className="last-col">
              {numeral(this.props.rating.lsi).format('0.00')}
            </td>
          </tr>
          <tr>
            <th className="first-col">1 period ago</th>
            <td>
              {capitalizeFirstLetter(this.props.rating.prev1Rating || 'none')}
            </td>
            <td>{numeral(this.props.rating.prev1Buy).format('0')}%</td>
            <td>{numeral(this.props.rating.prev1Hodl).format('0')}%</td>
            <td>{numeral(this.props.rating.prev1Sell).format('0')}%</td>
            <td className="last-col">
              {numeral(this.props.rating.prev1Lsi).format('0.00')}
            </td>
          </tr>
          <tr>
            <th className="first-col">2 periods ago</th>
            <td>
              {capitalizeFirstLetter(this.props.rating.prev2Rating || 'none')}
            </td>
            <td>{numeral(this.props.rating.prev2Buy).format('0')}%</td>
            <td>{numeral(this.props.rating.prev2Hodl).format('0')}%</td>
            <td>{numeral(this.props.rating.prev2Sell).format('0')}%</td>
            <td className="last-col">
              {numeral(this.props.rating.prev2Lsi).format('0.00')}
            </td>
          </tr>
          <tr>
            <th className="first-col">3 periods ago</th>
            <td>
              {capitalizeFirstLetter(this.props.rating.prev3Rating || 'none')}
            </td>
            <td>{numeral(this.props.rating.prev3Buy).format('0')}%</td>
            <td>{numeral(this.props.rating.prev3Hodl).format('0')}%</td>
            <td>{numeral(this.props.rating.prev3Sell).format('0')}%</td>
            <td className="last-col">
              {numeral(this.props.rating.prev3Lsi).format('0.00')}
            </td>
          </tr>
        </tbody>
      </table>
    )
  }
}

CoinRatingsTrendTable.propTypes = {
  rating: PropTypes.object,
}

export default CoinRatingsTrendTable
