import React, { Component } from 'react'
import numeral from 'numeral'
import Indicator from './indicator'
import PropTypes from 'prop-types'

class TopPick extends Component {
  render() {
    return (
      <div className="coin-rating-section-item top-pick">
        <Indicator
          value={numeral(this.props.topPickPrc).format('0')}
          text="TOP PICK"
        />
        <div className="content">
          <div className="title">TOP PICK</div>
          <div className="payload">
            {numeral(this.props.topPickPrc).format('0') + '%'} of users selected{' '}
            {this.props.coinName} as a Top Pick to outperform.
          </div>
        </div>
      </div>
    )
  }
}

TopPick.propTypes = {
  topPickPrc: PropTypes.number,
  coinName: PropTypes.string,
}

export default TopPick
