import React, { Component } from 'react'
import PropTypes from 'prop-types'
import './progressbar.scss'

class ProgreeBar extends Component {
  render() {
    const valueRatio =
      Math.round((this.props.value / this.props.maxValue) * 10000) / 100

    return (
      <div className="progressbar">
        <div className="progressbar-indicator">
          <div className="progressbar-indicator-thumb-wrapper">
            <div
              className="progressbar-indicator-thumb"
              style={{
                width: valueRatio + '%',
                backgroundColor: this.props.color,
              }}
            />
          </div>
        </div>

        <div className="progressbar-value">{Math.round(valueRatio) + '%'}</div>
      </div>
    )
  }
}

ProgreeBar.propTypes = {
  value: PropTypes.number,
  maxValue: PropTypes.number,
  color: PropTypes.string,
}

ProgreeBar.defaultValues = {
  value: 1,
  maxValue: 2,
}

export default ProgreeBar
