import React, { Component } from 'react'
import PropTypes from 'prop-types'
import NewsItem from './newsItem'
import { ClipLoader } from 'react-spinners'
import './coin-news-section.scss'

class CoinNewsSection extends Component {
  renderNews() {
    return (
      <div className="news-container">
        {this.props.news.map((n) => (
          <NewsItem key={n.id} news={n} />
        ))}
      </div>
    )
  }

  _onReadMoreClick = (e) => {
    if (!this.props.loading) {
      this.props.loadMore()
    }

    e.preventDefault()
  }

  render() {
    return (
      <div className="coin-news-section section">
        <h3 className="section-header">
          {(this.props.coin || {}).coinName} News
        </h3>
        <div className="section-body">
          {this.renderNews()}
          <div className="news-spinner">
            <ClipLoader
              sizeUnit="px"
              size={30}
              color="#DDDEE1"
              loading={this.props.loading}
            />
          </div>
        </div>
        <div className="section-footer">
          <a
            href="#"
            className="read-more-button"
            onClick={this._onReadMoreClick}
          >
            Read more news
          </a>
        </div>
      </div>
    )
  }
}

CoinNewsSection.propTypes = {
  coin: PropTypes.object,
  news: PropTypes.array,
  loading: PropTypes.bool,
  loadMore: PropTypes.func,
}

export default CoinNewsSection
