import React, { Component } from 'react'
import PropTypes from 'prop-types'
import CoinRatingsPage from './coinRatingsPage'
import coinRatingsService from './services/coinRatingsService'

class CoinRatingsPageContainer extends Component {
  state = { coinname: null, coinRatingData: null, loaded: false }

  async componentDidMount() {
    const coinName = this.props.match.params.coinName

    this.setState((state) => ({ ...state, coinName }))
    this.loadCoinReport(coinName)
  }

  async componentDidUpdate(prevProps) {
    const coinName = this.props.match.params.coinName

    if (coinName !== prevProps.match.params.coinName) {
      this.loadCoinReport(coinName)
    }
  }

  loadCoinReport = async (coiName) => {
    if (coiName) {
      this.setState((state) => ({ ...state, loaded: false }))
      const coinRatingData = await coinRatingsService.getCoinRatingData(coiName)
      this.setState((state) => ({ ...state, coinRatingData, loaded: true }))
    }
  }

  render() {
    return (
      <CoinRatingsPage
        coinRatingData={this.state.coinRatingData}
        loaded={this.state.loaded}
      />
    )
  }
}

CoinRatingsPageContainer.propTypes = {
  match: PropTypes.object,
}

export default CoinRatingsPageContainer
