import React, { Component } from 'react'
import PropTypes from 'prop-types'
import classnames from 'classnames'
import numeral from 'numeral'
import Indicator from './indicator'
import ProgressBar from './../progressBar'

class UserConsensus extends Component {
  render() {
    const max = Math.max(
      this.props.buy || 0,
      this.props.hodl || 0,
      this.props.sell || 0,
    )
    const maxIndicator =
      max === this.props.buy
        ? 'BUY'
        : max === this.props.hodl
          ? 'HODL'
          : max !== 0
            ? 'SELL'
            : 'NONE'

    return (
      <div className="coin-rating-section-item user-consensus">
        <Indicator value={numeral(max).format()} text={maxIndicator} />
        <div className="content">
          <div className="title">USER CONSENSUS</div>
          <div className="payload">
            <div
              className={classnames('progress-value', 'green', {
                active: maxIndicator === 'BUY',
              })}
            >
              <ProgressBar value={this.props.buy || 0} maxValue={100} />
              <div className="label">BUY</div>
            </div>
            <div
              className={classnames('progress-value', 'blue', {
                active: maxIndicator === 'HODL',
              })}
            >
              <ProgressBar value={this.props.hodl || 0} maxValue={100} />
              <div className="label">HODL</div>
            </div>
            <div
              className={classnames('progress-value', 'red', {
                active: maxIndicator === 'SELL',
              })}
            >
              <ProgressBar value={this.props.sell || 0} maxValue={100} />
              <div className="label">SELL</div>
            </div>
          </div>
        </div>
      </div>
    )
  }
}

UserConsensus.propTypes = {
  buy: PropTypes.number,
  sell: PropTypes.number,
  hodl: PropTypes.number,
}

export default UserConsensus
