import React, { Component } from 'react'
import PropTypes from 'prop-types'
import CoinNewsSection from './coinNewsSection'
import newsService from './services/newsService'

class CoinNewsSectionContainer extends Component {
  state = { news: [], loading: true, newsCategory: null }

  async componentDidMount() {
    await this.loadNews()
  }

  async componentDidUpdate(prevProps) {
    const coinName = this.getCoinName()

    if (coinName !== (prevProps.coin || {}).name) {
      await this.loadNews()

      this.setState((state) => ({ ...state, newsCategory: null }))
    }
  }

  getCoinName = () => {
    return (this.props.coin || {}).name
  }

  getNewsCategory = () => {
    return this.state.newsCategory || this.getCoinName()
  }

  loadNews = async () => {
    const newsCategory = this.getNewsCategory()

    if (newsCategory) {
      this.setState((state) => ({ ...state, news: [], loading: true }))
      const news = await newsService.getNews(newsCategory, 3, 0)

      if (news.length === 0) {
        return this.setState(
          (state) => ({ ...state, newsCategory: 'Altcoin' }),
          this.loadNews,
        )
      }

      this.setState((state) => ({ ...state, news, loading: false }))
    }
  }

  loadMoreNews = async () => {
    const newsCategory = this.getNewsCategory()

    if (newsCategory) {
      this.setState((state) => ({ ...state, loading: true }))

      const news = await newsService.getNews(
        newsCategory,
        6,
        this.state.news.length,
      )

      this.setState((state) => ({
        ...state,
        news: [...state.news, ...news],
        loading: false,
      }))
    }
  }

  render() {
    return (
      <CoinNewsSection
        {...this.props}
        news={this.state.news}
        loading={this.state.loading}
        loadMore={this.loadMoreNews}
      />
    )
  }
}

CoinNewsSectionContainer.propTypes = {
  coin: PropTypes.object,
}

export default CoinNewsSectionContainer
