import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import priceService from './../../../services/priceService'
import { formatUSD } from './../../../services/moneyService'
import PropTypes from 'prop-types'
import Bar from './bar'
import ColoredCell from './coloredCell'
import { rootPath } from './../../rootPage'

const _getForecastValue = (targetPrice, currentPrice) => {
  const forecastValue = (targetPrice / currentPrice - 1) * 100
  const finalValue = isFinite(forecastValue) ? forecastValue : 0
  return {
    forecast: finalValue.toFixed(0),
    absoluteForecast: Math.abs(finalValue).toFixed(0),
  }
}
class TopCoinCard extends Component {
  state = { currentPrice: 0 }

  componentDidMount() {
    if (this.props.price) {
      this.setState((state) => ({ ...state, currentPrice: this.props.price }))
    }

    if (this.props.coinName) {
      priceService.onPriceChange(
        this.props.coinName,
        'USD',
        this._onPriceChange,
      )
    }
  }

  componentWillUnmount() {
    if (this.props.coinName) {
      priceService.removeListener(this._onPriceChange)
    }
  }

  _onPriceChange = ({ price }) => {
    this.setState((state) => ({ ...state, currentPrice: price }))
  }

  render() {
    const {
      rank,
      coinName,
      image,
      buy,
      sell,
      hodl,
      rating,
      priceTarget,
    } = this.props
    const formatedPrice = formatUSD(this.state.currentPrice)
    const formatedPriceTarget = formatUSD(priceTarget)
    const projection = _getForecastValue(priceTarget, this.state.currentPrice)

    return (
      <div className="rounded card-wrapper">
        <div className="card-header">
          <div className="flex">
            <img src={image} />
            <div className="title-wrapper">
              <Link
                to={`${rootPath}-${(coinName || '').toLowerCase()}`}
                className="name"
              >
                {coinName}
              </Link>
              <span className="rating">
                RATING{' '}
                <span className={`${rating} rounded`}>
                  {rating.toUpperCase()}
                </span>
              </span>
            </div>
          </div>
          <span className="rank">#{rank}</span>
        </div>
        <div className="card-section">
          <span className="title">USER CONSENSUS</span>
          <div className="bars">
            <div className="flex bar-line">
              <Bar percentage={buy} barClass="green" />
              <span className={rating === 'buy' ? 'green' : 'grey'}>
                {buy ? `${buy}%` : '-'}
              </span>
              <span className={rating === 'buy' ? 'green' : 'grey'}>BUY</span>
            </div>
            <div className="flex bar-line">
              <Bar percentage={hodl} barClass="blue" />
              <span className={rating === 'hodl' ? 'blue' : 'grey'}>
                {hodl ? `${hodl}%` : '-'}
              </span>
              <span className={rating === 'hodl' ? 'blue' : 'grey'}>HODL</span>
            </div>
            <div className="flex bar-line">
              <Bar percentage={sell} barClass="red" />
              <span className={rating === 'sell' ? 'red' : 'grey'}>
                {sell ? `${sell}%` : '-'}
              </span>
              <span className={rating === 'sell' ? 'red' : 'grey'}>SELL</span>
            </div>
          </div>
        </div>
        <div className="card-footer">
          <span className="title">FORECAST</span>
          <div className="flex forecast">
            <div className="forecast-block">
              <span className="header">PRICE</span>
              <ColoredCell
                value={this.state.currentPrice}
                text={formatedPrice}
              />
            </div>
            <div className="forecast-block">
              <span className="header">TARGET</span>
              <span> {priceTarget ? formatedPriceTarget : '-'}</span>
            </div>
            <div className="forecast-block">
              <span className="header">PROJECTION</span>
              {priceTarget ? (
                <span
                  className={
                    projection.forecast > 0
                      ? 'green projection'
                      : 'red projection'
                  }
                >
                  {projection.forecast > 0 ? (
                    <span className="triangle">&#9650;</span>
                  ) : (
                    <span className="triangle">&#9660;</span>
                  )}
                  {Math.abs(projection.forecast)}%
                </span>
              ) : (
                <span className="projection">-</span>
              )}
            </div>
          </div>
        </div>
      </div>
    )
  }
}

TopCoinCard.propTypes = {
  coinName: PropTypes.string,
  rank: PropTypes.number,
  image: PropTypes.string,
  priceTarget: PropTypes.number,
  price: PropTypes.number,
  buy: PropTypes.number,
  sell: PropTypes.number,
  hodl: PropTypes.number,
  rating: PropTypes.string,
  target: PropTypes.number,
}

export default TopCoinCard
