import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { FaTwitter } from 'react-icons/fa'
import UserConsensus from './userConsensus'
import Forecast from './forecast'
import TopPick from './topPick'
import './coin-rating-section.scss'

class CoinRatingSection extends Component {
  share = (e) => {
    const coinName = this.props.coin.coinName
    const symbol = this.props.coin.symbol
    const rating =
      ((this.props.coinRatingData || {}).rating || {}).rating || 'None'
    const buy = ((this.props.coinRatingData || {}).rating || {}).buy || 0
    const sell = ((this.props.coinRatingData || {}).rating || {}).sell || 0
    const hodl = ((this.props.coinRatingData || {}).rating || {}).hodl || 0
    const priceTarget =
      ((this.props.coinRatingData || {}).rating || {}).priceTarget || 0

    const shareText = `#${coinName} RATING @BuySellHodlApp \n$${symbol} Rating = ${rating} \n${buy}% Say Buy \n${hodl}% Say #HODL \n${sell}% Say Sell \nPrice Target = ${priceTarget} \n#CryptoRatings #CryptoNews #BuySellHODL`

    window.open(
      'https://twitter.com/intent/tweet?text=' + encodeURIComponent(shareText),
      'Share on twitter',
      'height=350,width=500',
    )

    e.preventDefault()
  }

  render() {
    const rating = (this.props.coinRatingData || {}).rating || {}

    return (
      <div className="coin-rating-section section">
        <h3 className="section-header">
          {(this.props.coin || {}).coinName} Rating
          <span className="rating green">
            {(rating.rating || '').toUpperCase()}
          </span>
        </h3>
        <div className="section-body">
          <UserConsensus
            buy={rating.buy}
            sell={rating.sell}
            hodl={rating.hodl}
          />
          <Forecast coinRatingData={this.props.coinRatingData} />
          <TopPick
            topPickPrc={rating.topPickPrc || 0}
            coinName={(this.props.coin || {}).coinName}
          />
        </div>
        <div className="section-footer">
          <a onClick={this.share} href="#" className="share-twitter-link">
            <FaTwitter /> Share on twitter
          </a>
        </div>
      </div>
    )
  }
}

CoinRatingSection.propTypes = {
  coinRatingData: PropTypes.object,
  coin: PropTypes.object,
}

export default CoinRatingSection
