const getNews = async (symbol, limit, offset) => {
  const response = await fetch(
    `https://app.buysellhodlapp.com/api/news?where=categories:contain:${symbol}&limit=${limit}&offset=${offset}&sort=publishedAt:desc`,
  )
  const json = await response.json()

  return json.data.items
}

export default {
  getNews,
}
