import { valuationLabels, bitcoinPredictLabels } from './../constants'

export const coinMapper = (coin = {}) => {
  if (!coin.rating) {
    coin.rating = {
      priceTarget: 0,
      buy: 0,
      sell: 0,
      hodl: 0,
      rating: '-',
    }
  }
  return {
    price: coin.price,
    priceTarget: (coin.rating || {}).priceTarget,
    rank: coin.totalVolumeSortOrder,
    coinName: coin.name,
    fullName: (coin.fullName || '').substring(0, (coin.fullName || '').indexOf('(') - 1),
    image: coin.imageUrl,
    buy: (coin.rating || {}).buy || 0,
    sell: (coin.rating ||  {}).sell || 0,
    hodl: (coin.rating || {}).hodl || 0,
    rating: (coin.rating || {}).rating || 0,
  }
}

export const confidenceMapper = (confidence = {}) => ({
  buy: (confidence.buy || 0).toFixed(0),
  sell: (confidence.sell || 0).toFixed(0),
  hodl: (confidence.hodl || 0).toFixed(0),
})

export const valuationMapper = (valuation = {}) =>
  Object.keys(valuation).map((key) => ({
    label: valuationLabels[key],
    value: valuation[key],
  }))

export const bitcoinPredictMapper = (btcPredict = {}) =>
  Object.keys(btcPredict)
    .map((key) => ({
      label: bitcoinPredictLabels[key],
      value1: btcPredict[key][0],
      value2: btcPredict[key][1],
    }))
    .filter(({ label }) => label !== undefined)
    .reverse()
