import ee from 'event-emitter'
import feedSubscriber from './feedSubscriber'

const createKey = (fromSymbol, toSymbol) => {
  return fromSymbol + '~' + toSymbol
}

class PriceService {
  constructor() {
    this._subscriptions = {}
    this.disabled = document.location.href.includes('live-update=false')

    feedSubscriber.on('message', this._onFeedMessage)
  }

  onPriceChange(fromSymbol, toSymbol, callback) {
    if (
      typeof fromSymbol !== 'string' ||
      typeof toSymbol !== 'string' ||
      typeof callback !== 'function'
    ) {
      return
    }

    const key = createKey(fromSymbol, toSymbol)

    if (this._subscriptions[key]) {
      if (!this._subscriptions[key].callbacks.find((x) => x === callback)) {
        this._subscriptions[key].callbacks.push(callback)
      }
    } else {
      this._subscriptions[key] = {
        fromSymbol,
        toSymbol,
        feedSubscription: '2~' + key,
        callbacks: [callback],
      }

      feedSubscriber.subscribe('2~' + key)
    }
  }

  removeListener(callback) {
    if (typeof callback !== 'function') {
      return
    }

    Object.keys(this._subscriptions).forEach((key) => {
      const sub = this._subscriptions[key]
      sub.callbacks = sub.callbacks.filter((c) => c !== callback)

      if (sub.callbacks.length === 0) {
        feedSubscriber.unsubscribe(sub.feedSubscription)
        delete this._subscriptions[key]
      }
    })
  }

  _onFeedMessage = (message) => {
    if (!this.disabled && message.channel === '2' && message.price) {
      const fromSymbol = message.fromSymbol
      const toSymbol = message.toSymbol
      const key = createKey(fromSymbol, toSymbol)

      if (this._subscriptions[key]) {
        this._subscriptions[key].callbacks.forEach((c) => c(message))
      }
    }
  }
}

export default ee(new PriceService())
