import React, { Component } from 'react'
import PropTypes from 'prop-types'
import classnames from 'classnames'
import numeral from 'numeral'
import priceService from './../../services/priceService'
import './price-indicator.scss'

class PriceIndicator extends Component {
  state = { classname: '', price: 0 }

  componentDidMount() {
    if (this.props.fromSymbol && this.props.toSymbol) {
      priceService.onPriceChange(
        this.props.fromSymbol,
        this.props.toSymbol,
        this._onPriceChange,
      )
    }

    if (this.props.initialPrice && !isNaN(this.props.initialPrice)) {
      this.setState((state) => ({ ...state, price: +this.props.initialPrice }))
    }
  }

  componentDidUpdate(prevProps, prevState) {
    if (
      this.props.fromSymbol &&
      this.props.toSymbol &&
      prevProps.fromSymbol !== this.props.fromSymbol &&
      prevProps.toSymbol !== this.props.toSymbol
    ) {
      priceService.removeListener(this._onPriceChange)
      priceService.onPriceChange(
        this.props.fromSymbol,
        this.props.toSymbol,
        this._onPriceChange,
      )
    }

    if (
      this.props.initialPrice &&
      this.props.initialPrice !== prevProps.initialPrice &&
      !isNaN(this.props.initialPrice)
    ) {
      this.setState((state) => ({ ...state, price: +this.props.initialPrice }))
    }

    if (
      prevState.price &&
      this.state.price &&
      prevState.price !== this.state.price
    ) {
      this.startAnimation(this.state.price - prevState.price)
    }
  }

  componentWillUnmount() {
    priceService.removeListener(this._onPriceChange)
  }

  startAnimation(diff) {
    clearTimeout(this._timer)

    this.setState(
      (state) => ({ ...state, classname: '' }),
      () => {
        this._timer = setTimeout(() => {
          this.setState((state) => ({
            ...state,
            classname: diff < 0 ? 'green' : 'red',
          }))
        }, 10)
      },
    )
  }

  render() {
    return (
      <span className={classnames('price-indicator', this.state.classname)}>
        {numeral(this.state.price).format('$0,0.00')}
      </span>
    )
  }

  _onPriceChange = ({ price }) => {
    if (!isNaN(price)) {
      this.setState((state) => ({ ...state, price: +price }))
    }
  }
}

PriceIndicator.propTypes = {
  fromSymbol: PropTypes.string,
  toSymbol: PropTypes.string,
  initialPrice: PropTypes.number,
}

export default PriceIndicator
