import React, { Component } from 'react'
import PropTypes from 'prop-types'
import CoinRatingsTrendTable from './coinRatingsTrendTable'
import './coin-ratings-trend-section.scss'

class CoinRatingsTrendSection extends Component {
  render() {
    const rating = (this.props.coinRatingData || {}).rating || {}

    return (
      <div className="coin-ratings-trend-section section">
        <h3 className="section-header">
          {(this.props.coin || {}).coinName} Ratings Trend
        </h3>
        <div className="section-body">
          <CoinRatingsTrendTable rating={rating} />
        </div>
      </div>
    )
  }
}

CoinRatingsTrendSection.propTypes = {
  coinRatingData: PropTypes.object,
  coin: PropTypes.object,
}

export default CoinRatingsTrendSection
