import React, { Component } from 'react'
import RatingsPageView from './RatingsPage'
import moment from 'moment'
import * as mappers from './helpers/mappers'
import * as mocks from './constants'

export default class RatingsPage extends Component {
  state = {
    confidence: {},
    top3: [],
    rest7: [],
    valuation: [],
    bitcoinPredict: [],
    date: moment()
      .format('MMM D, YYYY')
      .toUpperCase(),
  }
  componentDidMount = () => {
    this.loadData()
  }

  loadData = async () => {
    const response = await fetch(mocks.requestString)
    const body = await response.json()

    const { valuation, bitcoin, cryptoConfidenceIndex } = body.data
    //const coins = (body.data.coins || []).map(mappers.coinMapper).filter((coin) => coin !== undefined)

    this.setState(state => ({
      ...state,
      confidence: mappers.confidenceMapper(cryptoConfidenceIndex),
      //top3: coins.slice(0, 3),
      //rest7: coins.slice(3, coins.length),
      valuation: mappers.valuationMapper(valuation).reverse(),
      bitcoinPredict: mappers.bitcoinPredictMapper(bitcoin),
      loaded: true,
    }))
  }
  render() {
    return (
      <RatingsPageView
        confidence={this.state.confidence}
        topCoins={this.state.top3}
        restCoins={this.state.rest7}
        valuation={this.state.valuation}
        bitcoinPredict={this.state.bitcoinPredict}
        colors={mocks.colors}
        date={this.state.date}
        loaded={this.state.loaded}
      />
    )
  }
}
