import React, { Component } from 'react'
import PropTypes from 'prop-types'

class CoinAnalysisSection extends Component {
  render() {
    return (
      <div className="coin-analysis-section section">
        <h3 className="section-header">
          {(this.props.coin || {}).coinName} Analysis
        </h3>
        <div
          className="section-body"
          dangerouslySetInnerHTML={{
            __html: (this.props.coinSettings || {}).analysis || 'No Data',
          }}
        />
      </div>
    )
  }
}

CoinAnalysisSection.propTypes = {
  coin: PropTypes.object,
  coinSettings: PropTypes.object,
}

export default CoinAnalysisSection
