import React from 'react'
import { BrowserRouter as Router } from 'react-router-dom'
import { Switch, Route, Redirect } from 'react-router'
import CoinRatings from './coin-ratings'
import CommonRatings from './common-ratings'

export const rootPath = '/crypto-ratings'

const AppRouter = () => (
  <Router>
    <Switch>
      <Route path={rootPath} exact component={CommonRatings} />
      <Route path={`${rootPath}/:coinName`} exact component={CoinRatings} />
      <Route path={`${rootPath}-:coinName`} exact component={CoinRatings} />
      <Redirect from="/" to={rootPath} />
    </Switch>
  </Router>
)

export default AppRouter
